
import React ,{useEffect, useState } from "react"
import { toast } from "react-toastify"
import { Link ,useNavigate,useParams} from "react-router-dom"
import ApiServices ,{BASE_URL} from "../../ApiServices"
import {ClipLoader} from "react-spinners"

const override= {

  margin: "0 auto",
  marginTop: "250px",
  marginBottom: '200px',
  display:'flex',
  justifyContent: 'center',
  alignItems: 'center',
  overflow:"hidden"
};

export default function ViewReports() {
const [x,setX]=useState(false)
// const [data,setData]=useState()
const [appointmentId,setAppointmentId]=useState()
const [clientName,setClientName]=useState()
const [clientEmail,setClientEmail]=useState()
const [categoryName,setCategoryName]=useState()
const [duration,setDuration] = useState()
const [description,setDescription] = useState()
const [technology,setTechnology] = useState()
const [postedOn,setPostedOn] = useState()
const [name,setName] = useState()

const [previousImage,setPreviousImage]=useState("")
let [color, setColor] = useState("#2c4964;");
const [isLoading, setIsLoading] = useState(true);
const param=useParams()
console.log(param);
const id=param.id 
  useEffect(()=>{
    let data={
        _id:id
    }
    ApiServices.viewSingleProject(data).then((res)=>{
      console.log("Result is",res.data.data)
      setClientName(res.data.data.clientId.name)
      setClientEmail(res.data.data.clientId.email)
      setCategoryName(res.data.data.categoryId.name)
      setDuration(res.data.data.duration)
      setDescription(res.data.data.description)
      setTechnology(res.data.data.technology)
      setName(res.data.data.name)
      setPreviousImage(res.data.data.attachment)
      setPostedOn(res.data.data.createdAt)

      // console.log(res.data.data)
  })
    .catch((err)=>{console.log(err)})
    setTimeout(() => {
      setIsLoading(false);
    }, 1500);
  },[x])
 
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    // Example format: "MM/DD/YYYY"
    const formattedDate = `${date.getMonth() + 1}/${date.getDate()}/${date.getFullYear()}`;
    return formattedDate;
};

 
  return (
    <>
    {isLoading &&(
    <ClipLoader
    color={color}
    loading={isLoading}
    cssOverride={override}
    size={100}
    aria-label="Loading Spinner"
    data-testid="loader"
  />
  )}
    {!isLoading &&(
    <>
    <main className="main my-5">
  {/* ======= Departments Section ======= */}
  <section id="departments" className="departments">
 
    <div className="container" data-aos="fade-up">
    <div className="section-header mt-2">
    <h2>{name.toUpperCase()}</h2>
    </div>

      <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>

      <div class="card" style={{width: "30rem",margin:'0 auto'}}>
  <iframe class="card-img-top" src={BASE_URL+previousImage} alt="Card image cap" style={{height:'28rem', width:'auto'}}></iframe>
  <div class="card-body">
    <h5 class="card-title">{categoryName.toUpperCase()}</h5>
    <p>{description}</p>
    <p>{technology.toUpperCase()}</p>
    <p><span class="badge badge-info text-light" style={{backgroundColor:'#071952'}}> Posted On </span>{formatDate(postedOn)}</p>
    <p><span class="badge badge-info text-light" style={{backgroundColor:'#071952'}}> Posted By </span>{clientName},{clientEmail}</p>
    {/* <center>
    <Link to={'/admin/viewBids/'}>
    <button className="btn text-center text-light" style={{backgroundColor:"#071952", width:'50%'}}>
     View Bids
    </button>
    </Link>
    </center> */}


  </div>
</div>
              
      </div>

    </div>
  </section>
  {/* End Departments Section */}
  </main>
  
  </>
    )}
</>

  )
}
